<template>
    <div class="container col-md-7">
              <b-alert 
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged">{{mensaje.texto}}</b-alert>
      <form @submit.prevent="Agregar() " class=" card mt-4" >
        <h3 class="title card-header">Crear tu Smoothie</h3>
        <div class="card-body">
<div class="input-group mt-2 ">
            <div class="col-md-5">
                <h4>Nombre:</h4>
            </div>
            <div class="col-md-7">
                <input type="text" id=""  v-model="smoothies.name" class="form-control" placeholder="" required>
            </div>
        </div>
        <div class="input-group ">
            <div class="col-md-5 mt-2">
            <h4>Agregar frutas:</h4>
            </div>
            <div class="col-md-5 ">
            <select ref="listfruits" class="form-control">
                <option name="fruta"  v-for="(frutas,index) in fruits" :key="index" > {{frutas.name}} - {{frutas.value}} </option> 
            </select>
            </div>
            <v-btn name="agrega mt-1" class="btn btn-primary my-2 mr-4" @click="agregarFruta($refs.listfruits.value)"> <i class="fas fa-plus"></i></v-btn>
        </div>
        
        <h4 class="my-3">Frutas:</h4>
        <div class="input-group mt-2 col-md-12">
            <textarea name="" id="" class="form-control" disabled v-model="smoothies.fruits" cols="30" rows="5"></textarea>

        </div>

        <div class="input-group mt-2">
            <div class="col-md-5">
            <h4>Selecciona Liquido:</h4>
            </div>
            <select  v-model="smoothies.liquid" required class="form-control">
                <option v-for="liquidos in liquids">{{liquidos.name}} - {{liquidos.value}} </option>
            </select>
        </div>
        <div class="input-group mt-2">
            <div class="col-md-5">
            <h4>Selecciona proteina:</h4>
            </div>
            <select v-model="smoothies.protein " required class="form-control">
                <option v-for="proteinas in proteins">{{proteinas.name}}</option> 
            </select>
        </div>
        <div class="input-group mt-2">
            <div class="col-md-5">
            <h4>Selecciona sabor:</h4>
            </div>
            <input v-model="smoothies.taste" class="form-control" max="100" min="0" type="number" required >
        </div>
                  <v-btn class="success my-5 mx-5" type="submit">Crear Smoothie  <i class="fas fa-check-circle"></i></v-btn>
          <b-button class="warning my-5 " @click="limpiar">Limpiar  <i class="fas fa-trash"></i></b-button>
        </div>
        

      </form>
    </div>
</template>
<script>
export default {
    data(){
      return{
        dismissSecs:8,
        dismissCountDown:0,
        mensaje:{color:'',texto:''},
        fruits:[
            {name:'Watermelon', value:6},
            {name:'Strawberry', value:5},
            {name:'Pineapple', value:8},
            {name:'Orange', value:10},
            {name:'Kiwi', value:7},
            {name:'Banana ', value:9},
            {name:'Limes', value:4},
        ],
        liquids:[
            {name:'Leche', value:4},
            {name:'Agua', value:0},
            {name:'Agua de coco', value:2},
            {name:'Leche de soya', value:3},
            {name:'Leche de almendras', value:1},
        ],
        proteins:[
            {name:'Vainilla'},
            {name:'Chocolate'},
            {name:'Coco'},
            {name:'Caramelo'},
            {name:'Sin sabor'}
        ],
        smoothies:{
            name:'',
            fruits:'',
            liquid:'',
            protein:'',
            heart:0,
            taste:100
        },
      }
  },

  methods: {
      alerta(color,mensaje){
          this.mensaje.color=color;
          this.mensaje.texto=mensaje;
          this.showAlert();
      },
      Agregar(){
        this.smoothies.heart+=parseInt(this.smoothies.liquid.substring(this.smoothies.liquid.indexOf('- ')+2));
        this.smoothies.liquid=this.smoothies.liquid.substring(0,this.smoothies.liquid.indexOf('-'));
          this.axios.post('/nuevo',this.smoothies).then(res=>{
            this.alerta('success','Smoothie creado');
            this.limpiar();
          }).catch(e=>{
              this.alerta('danger','Error al crear smoothie ');
              });
              
      },
      countDownChanged(dismissCountDown){
          this.countDownChanged=dismissCountDown;
      },
      showAlert(){
          this.dismissCountDown=this.dismissSecs;
      },
      agregarFruta(fruta){
          if(this.smoothies.fruits===''){
            this.smoothies.fruits=fruta.substring(0,fruta.indexOf('-'));
          }
         else{
              this.smoothies.fruits=this.smoothies.fruits+', '+fruta.substring(0,fruta.indexOf('-'));
         }
          this.smoothies.heart+=parseInt(fruta.substring(fruta.indexOf('- ')+2));
      },
      limpiar(){
        this.smoothies.name='';
        this.smoothies.fruits='';
        this.smoothies.fruits='';
        this.smoothies.heart=0;
        this.smoothies.taste=100;
      }
  },
  computed: {
  },
}
</script>
<style>
.btn-primary{
  background: #d442c8;
  color: #ffffff;
  border-color: #d442c8;
  
  border-radius:40px;
  font-size: 15pt;
}
.btn-primary:hover{
  background: #a31798;
  color: #d442c8;
  border-color: #d442c8;
}

</style>